import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpHeaders } from '@angular/common/http'
import { EndpointFactory } from '../../services/endpoint-factory.service';
import { ConfigurationService } from '../../services/configuration.service';
import { AuthService } from '../../services/auth.service';
@Component({
    selector: 'upload-component',
    templateUrl: './upload.component.html'
})
export class UploadComponent implements OnInit{
    
    public progress: number;
    public message: string;
    private textbox: string;
    constructor(private http: HttpClient, private authService: AuthService) { }
    ngOnInit() {
        this.getListFiles();
    }
    upload(files) {
        if (files.length === 0)
            return;

        const formData = new FormData();

        for (let file of files)
            formData.append(file.name, file);
        let headers = new HttpHeaders({
            'Authorization': 'Bearer ' + this.authService.accessToken,
            'Accept': `application/vnd.iman.v${EndpointFactory.apiVersion}+json, application/json, text/plain, */*`,
            'App-Version': ConfigurationService.appVersion
        });
        const uploadReq = new HttpRequest('POST', `api/upload`, formData, {
            reportProgress: true,
            headers: headers
        });

        this.http.request(uploadReq).subscribe(event => {
            if (event.type === HttpEventType.UploadProgress)
                this.progress = Math.round(100 * event.loaded / event.total);
            else if (event.type === HttpEventType.Response)
                this.getListFiles();
        });
    }

    private getListFiles() {

        const uploadReq = new HttpRequest('GET', `api/files`, {
            reportProgress: true
        });

        this.http.request(uploadReq).subscribe(event => {
            if (event.type === HttpEventType.Response)
                this.message = event.body.toString();
        });
    }

    private delete() {
        let headers = new HttpHeaders({
            'Authorization': 'Bearer ' + this.authService.accessToken,
            'Accept': `application/vnd.iman.v${EndpointFactory.apiVersion}+json, application/json, text/plain, */*`,
            'App-Version': ConfigurationService.appVersion
        });
        const uploadReq = new HttpRequest('DELETE', `api/files/` + this.textbox, {
            reportProgress: true,
            headers: headers
        });

        this.http.request(uploadReq).subscribe(event => {
            if (event.type === HttpEventType.UploadProgress)
                this.progress = Math.round(100 * event.loaded / event.total);
            else if (event.type === HttpEventType.Response)
                this.getListFiles();
        });
    }
}



